.profileContainer {
  position: relative;

  display: flex;
  flex-direction: column;
  flex-grow: 1;
  align-self: flex-start;

  width: 100%;
  height: 100%;
  padding: 60px 0px 0px 0px;

  text-align: center;
}
.profile {
  &__content {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    align-self: center;

    padding: 30px 40px;

    background: rgb(240 240 240);
    border-radius: 20px;
  }
  &__linkPacks {
    position: absolute;
    top: 24px;
    left: 0;

    display: flex;
    align-items: center;

    font-weight: 500;
    line-height: 24px;
    color: inherit;

    transition: all 0.2s ease;
    &:hover {
      color: white;
    }
  }
  &__arrow {
    margin: 0px 10px 0px 0px;
  }
  &__title {
    margin: 0px 0px 10px 0px;

    font-size: 26px;
    font-weight: 600;
    &::first-letter {
      text-transform: uppercase;
    }
  }
  &__avatarBlock {
    position: relative;
  }
  &__pic {
    cursor: pointer;

    position: relative;

    overflow: hidden;

    width: 110px;
    height: 110px;

    border-radius: 100%;
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    object-fit: cover;
  }
  &__avatarIcon {
    cursor: pointer;

    position: absolute;
    right: 0;
    bottom: 0;

    width: 30px !important;
    height: 30px !important;
    padding: 4px;

    color: grey;

    background: white;
    border: 1px solid grey;
    border-radius: 30px !important;
  }
  &__userName {
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none;

    font-size: 20px;
    font-weight: 500;
    line-height: 24px;
    text-transform: capitalize;
  }
  &__marker {
    cursor: pointer;

    margin: 0px 0px 0px 10px !important;

    color: grey;
  }
  &__userEmail {
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none;

    color: grey;
  }
  &__button {
    align-self: center;

    padding: 5px 20px !important;

    color: black !important;

    background: white !important;
  }
  &__buttonIcon {
    margin: 0px 10px 0px 0px;
  }
}
